<template>
  <div id="app" :is="layout">
  </div>
</template>

<script>
  import Base from "./components/layout/Base";


  document.addEventListener('DOMContentLoaded', function() {
    // let imgs = Array.from(document.getElementsByTagName('img'));
    let imgs = document.getElementsByTagName('img');
    let check = [];

    console.log(document, document.getElementsByTagName('img'));

    imgs.forEach(img => {
      // console.dir(img);
      img.addEventListener('load', function () {
        // console.log(e);
        check.push('ok');
        if (check.length === imgs.length) {
          console.log("OK");
        }
      })
    })

  });



  export default {
    components: {Base},
    data() {
      return {
        layout: "Base",
        timer: false,
        imgs: []
      }
    },
    methods: {
      responsive () {
        let sm = window.matchMedia('(min-width: 5px)');
        let md = window.matchMedia('(min-width: 768px)');
        let lg = window.matchMedia('(min-width: 992px)');

        this.$store.commit('updateBreakpoint', [
          {
            name: 'isMdUp',
            result: md.matches
          },
          {
            name: 'isSmUp',
            result: sm.matches
          },
          {
            name: 'isLgUp',
            result: lg.matches
          },
        ])
      },
      getImgs() {
        this.imgs.push()
      }
    },
    mounted() {
      console.dir(this);
      window.addEventListener('resize', this.responsive);
      this.responsive();
      // this.timer = setInterval(() => {
      //   let img = document.querySelectorAll('img');
      //   if(img.length) {
      //     clearInterval(this.timer)
      //   }
      // }, 50)


    },
    beforeDestroy() {
      window.removeEventListener('resize', this.responsive);
    }
  }
</script>

<style lang="scss">
  @import "assets/scss/style";

  @include media-breakpoint-down(sm) {
    #app {
      padding-bottom: 118px;
    }
  }
</style>

